}

@keyframes zoom {
    0%{
        transform: scale(1);
    }
    100%{
        transform: scale(1.5);
    }
}

@keyframes changesize {
    0%{
        width: 50%;
    }
    100%{
        width: 90%;
    }
}

@keyframes hide {
    0%{
        opacity: 100%;
    }
    100%{
        opacity: 0%;
    }
}

main{
    margin: auto;
    width: 50%;
    max-width: 700px;
    height: 75%;
    background: rgba(184, 209, 233, 0.1);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(5px);
    border: 2px solid rgba(184, 209, 233, 0.5);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

#logo{
    width: 100px;
}

h1, p{
    color: #b8d1e9;
    font-family: 'Bungee', cursive;
    text-align: center;
    font-size: 90px;
}

h1{
    letter-spacing: 40px;
    margin: 0;
    margin-left: 42px;
}

p{
    font-family: "Montserrat";
    font-size: x-large;
    margin-bottom: 15px;
}



input{
    text-align: center;
	width: 270px;
	height: 30px;
    border: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	background: rgba(255, 255, 255, 0.05);
	box-shadow: 0 5px 5px rgba(0, 0, 0, 0.3);
	border-bottom: 1px solid rgba(255, 255, 255, 0.1);
	border-top: 1px solid rgba(255, 255, 255, 0.1);
	border-radius: 30px;
	padding: 10px;
	letter-spacing: 1px;
	text-decoration: none;
	overflow: hidden;
	font-family: "Montserrat";
    font-weight: 700;
    color: #282e32;
    font-size: x-large;
	z-index: 1;
	transition: 0.5s;
	backdrop-filter: blur(15px);
}

input::after{
    content: "";
    position: absolute;
	top: 0;
	left: 100;
	width: 50px;
	height: 100px;
	background: linear-gradient(to left, rgba(255, 255, 255, 0.5), transparent);
	transform: skewX(45deg) translate(0);
	transition: 0.5s;
	filter: blur(1px);
}

input:focus{
    outline: none;
}



.container .btn {
	position: relative;
	top: 0;
	left: 0;
	width: 300px;
	height: 75px;
	margin-left: -20px;
	display: flex;
	justify-content: center;
	align-items: center;
    font-family: "Montserrat";
}
.container .btn a {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	background: rgba(255, 255, 255, 0.05);
	box-shadow: 0 5px 5px rgba(0, 0, 0, 0.3);
	border-bottom: 1px solid rgba(255, 255, 255, 0.1);
	border-top: 1px solid rgba(255, 255, 255, 0.1);
	border-radius: 30px;
	padding: 10px;
	letter-spacing: 1px;
	text-decoration: none;
	overflow: hidden;
	color: #282e32;
    font-weight: 700;
    font-size: x-large;
	z-index: 1;
	transition: 0.5s;
	backdrop-filter: blur(15px);
}
.container .btn:hover a {
	letter-spacing: 3px;
    color: #b8d1e9;
}
.container .btn a::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 50%;
	height: 100%;
	background: linear-gradient(to left, rgba(255, 255, 255, 0.15), transparent);
	transform: skewX(45deg) translate(0);
	transition: 0.5s;
	filter: blur(1px);
}
.container .btn:hover a::before {
	transform: skewX(45deg) translate(200px);
}
.container .btn::before {
	content: "";
	position: absolute;
	left: 50%;
	transform: translatex(-50%);
	bottom: -5px;
	width: 30px;
	height: 10px;
	border-radius: 10px;
	transition: 0.5s;
	transition-delay: 0.5;
}
.container .btn:hover::before /*lightup button*/ {
	bottom: 0;
	height: 50%;
	width: 80%;
	border-radius: 30px;
}

.container .btn::after {
	content: "";
	position: absolute;
	left: 50%;
	transform: translatex(-50%);
	top: -5px;
	width: 30px;
	height: 10px;
	border-radius: 10px;
	transition: 0.5s;
	transition-delay: 0.5;
}
.container .btn:hover::after /*lightup button*/ {
	top: 0;
	height: 50%;
	width: 80%;
	border-radius: 30px;
}
.container .btn:nth-child(1)::before, /*chnage 1*/
.container .btn:nth-child(1)::after {
	background: #436389;
	box-shadow: 0 0 5px #436389, 0 0 15px #436389, 0 0 30px #436389,
		0 0 60px #436389;
}
